	    SAASimulatorComponent.prototype.createParameters = function (userEmail) {
	        var params = "SELECTED_SCHEMA=" + this.defaultOption.code;
	        // if (this.selectedAssignments.length > 0) {
	        //   params += "\nBACKENDNAME='" + this.assignment.code + "'";
	        //   params += "\nSELECTED_RULES=" + this.selectedRules.map(r => r.ruleName).join(",");
	        // }
	        var areAllMessagesSelected = this.calculTotalMessages() == this.totalSelectedMessages;
	        params += "\nALL_MESSAGES=" + (areAllMessagesSelected ? "YES" : "NO");
	        if (!areAllMessagesSelected) {
	            var allMessageIds = this.selectedMessages
	                .filter(function (message) { return !message.group || message.group.length == 0; })
	                .map(function (msg) { return msg.id; });
	            var messageGroup = this.selectedMessages
	                .filter(function (message) { return message.group && message.group.length > 0; })
	                .map(function (msg) { return "'" + msg.name + "'"; });
	            params += "\nMESSAGE_ID=" + allMessageIds.join(",");
	            params += "\nMESSAGE_GROUP=" + messageGroup.join(",");
	        }
	        params += "\nSEND_MAIL=" + (userEmail ? userEmail : "");
	        return params;
	    };
