	    SAASimulatorComponent.prototype.actionExecute = function () {
	        var _this = this;
	        this.logger.log("executiing simulation");
	        var start = new Date().getTime();
	        this.resultPendingStatus = 0;
	        //  Observable.interval(1).subscribe(x => this.currentValue = this.currentValue + 1);
	        var progressObserver;
	        var progress$ = Observable_1.Observable.create(function (observer) {
	            progressObserver = observer;
	        })
	            .share();
	        // let progress$: Subject<number> = new Subject<number>();
	        var startCounter = 0;
	        var rulesProcessed = 0;
	        // progress$.subscribe(p => {
	        //  let newProgress  = Math.floor(p);
	        //   this.currentValue = newProgress == 0 ? this.totalSelectedRules : newProgress;
	        //   this.logger.debug("with Share rules "+p+"% treated. newValue "+this.currentValue);
	        // },
	        // err => this.logger.error("progress err : "+ err.message,
	        // () => this.logger.log("progress$ done!")));
	        // this.logger.log("progress.subscribe done!")
	        //let executionResults = [];
	        this.executionResults = [];
	        var notHitMessages = [];
	        this.mergeAllMessages().subscribe(function (messages) {
	            _this.logger.debug("total messages selected to simulate = " + messages.length);
	            _this.messagesToProcess(messages).forEach(function (messageObs) {
	                var process = messageObs.flatMap(function (message) {
	                    var toParse = "params = " + message.itemMap + ' ';
	                    // ms = message.name;
	                    return _this.rulesToProcess()
	                        .map(function (result) {
	                        result.expressionToEvaluate = toParse + result.ruleExpression;
	                        result.paramsSize = toParse.length;
	                        result.messageName = message.name;
	                        try {
	                            result.messageReference = JSON.parse(String(message.itemMap))["swiftParameters_requestReference"];
	                        }
	                        catch (e) { }
	                        return result;
	                    });
	                })
	                    .map(function (result) { var value = _this.processRule(result); return value; });
	                //TODO: How to do to avoid double execution of process observable (one for each observer) 
	                // Adding a subject does not help
	                //let subject : Subject<SimulationResult> = new Subject<SimulationResult>(undefined, process);
	                var alreadyAddedResultMap = new Map();
	                var alreadyRemovedResultMap = new Map();
	                var notHitObservableArray = process
	                    .filter(function (result) {
	                    if (result.hit) {
	                        // this.logger.debug( result.messageName + " has a hit ");
	                        if (alreadyAddedResultMap.has(result.messageName)) {
	                            var removeSuccess = alreadyAddedResultMap.delete(result.messageName);
	                        }
	                        alreadyRemovedResultMap = alreadyRemovedResultMap.set(result.messageName, true);
	                        return false;
	                    }
	                    else {
	                        // this.logger.debug(" No hit received " + result.messageName);
	                        if (!alreadyAddedResultMap.has(result.messageName) && !alreadyRemovedResultMap.has(result.messageName)) {
	                            // this.logger.debug(" adding " + result.messageName + " already removed? " + (alreadyRemovedResultMap.has(result.messageName)));
	                            alreadyAddedResultMap = alreadyAddedResultMap.set(result.messageName, true);
	                            return true;
	                        }
	                        else {
	                            return false;
	                        }
	                    }
	                })
	                    .toArray();
	                if (_this.selectedMatch == "1") {
	                    var defaultValue = new simulation_1.SimulationResult(0, "", 0, "", "No rule(s) hit", 0, "", "", "");
	                    process = process.first(function (result, index) { return result.hit; }, function (result) { return result; }, defaultValue);
	                }
	                process.subscribe(function (res) {
	                    // this.currentValue = this.currentValue + 1;
	                    //this.logger.debug("receving "+this.currentValue + " for rule " + res.ruleName+" msg "+res.messageNameHit);        
	                    if (res.errorMsg) {
	                        _this.logger.error(" ruleCode " + res.ruleName + " => " + res.hit + " err " + res.errorMsg);
	                    }
	                    if (res.hit) {
	                        _this.executionResults = _this.executionResults.concat([res]);
	                        _this.resultPendingStatus = 0;
	                    }
	                }, function (err) {
	                    if (_this.selectedMatch == "1" && err.name === "EmptyError") {
	                        _this.logger.log("No hit found ");
	                    }
	                    else {
	                        _this.logger.error("error " + JSON.stringify(err) + " msg " + err.message);
	                    }
	                }, function () {
	                    // this.currentValue = 0;
	                    var end = new Date().getTime();
	                    _this.logger.debug("finished after " + (end - start) + "milliseconds");
	                    if (_this.executionResults.length == 0) {
	                        _this.resultPendingStatus = -1;
	                        _this.resultPendingMessage = "No hits found with current rule(s) and message(s) selection.";
	                    }
	                    notHitObservableArray.subscribe(function (re) {
	                        //Use map and filter instead of forEach ?? performance ?? 
	                        return re.forEach(function (r) {
	                            if (alreadyAddedResultMap.get(r.messageName)) {
	                                var noHitRes = new simulation_1.SimulationResult(0, "", 0, "", "No rule(s) hit", 0, "", "", "");
	                                noHitRes.messageNameHit = r.messageName;
	                                notHitMessages = notHitMessages.concat([noHitRes]);
	                            }
	                            //              this.logger.log("finally- " + r.messageName + " hit? " + alreadyAddedResultMap.get(r.messageName));
	                        });
	                    });
	                });
	            });
	            _this.logger.warn("THE FOR IS SYNCHRONOUS!!! ");
	            var hits = _this.executionResults.length;
	            _this.hitMessage = hits ? hits + " hit(s) on " + (hits + notHitMessages.length) + " simulation(s)" : " No rule(s) hit ";
	            _this.executionResults = _this.executionResults.concat(notHitMessages);
	        });
	    };
	    SAASimulatorComponent.prototype.processRule = function (res) {
	        try {
	            //      this.logger.debug(" evaluating " + res.expressionToEvaluate);
	            res.hit = this.RuleEvaluationParser.parse(res.expressionToEvaluate);
	            //     this.logger.debug(" after " + res.hit);
	            if (res.hit)
	                res.messageNameHit = res.messageName;
	            res.withError = false;
	            res.errorMsg = "";
	        }
	        catch (syntaxError) {
	            var location_1 = syntaxError.location.start;
	            // this.logger.log(" col " + location.column + " paramSize " + res.paramsSize);
	            var errorMsg = "Line " + location_1.line + ", Column " + (location_1.column - res.paramsSize) + ": ";
	            errorMsg += syntaxError.message || "Error while parsing";
	            res.hit = false;
	            res.withError = true;
	            res.errorMsg = errorMsg;
	        }
	        return res.clone();
	    };
	    SAASimulatorComponent.prototype.flatMap = function (array, f) {
	        return array.reduce(function (ys, x) {
	            return ys.concat(f.call(array, x));
	        }, []);
	    };
	    SAASimulatorComponent.prototype.mergeAllMessages = function () {
	        var _this = this;
	        var selectedGroupIds = this.selectedMessages
	            .filter(this.groupMessages)
	            .map(function (msg) { return msg.group; });
	        return this.amhRoutingService.loadFlatGroupMessages(selectedGroupIds)
	            .flatMap(function (resp) {
	            if (resp.found) {
	                _this.logger.debug("found resp OK " + JSON.stringify(resp.messages[0]));
	                return Observable_1.Observable.from([_this.selectedMessages
	                        .filter(_this.singleMessages).concat(resp.messages)]);
	            }
	            else {
	                _this.logger.debug("No group messages found");
	                return Observable_1.Observable.from([_this.selectedMessages.filter(_this.singleMessages)]);
	            }
	        });
	        // let allMessages = this.flatMap(this.selectedMessages,
	        //   message => message.group ? message.messages : [message]);
	    };
	    SAASimulatorComponent.prototype.messagesToProcess = function (allMessages) {
	        if (this.selectedMatch == "1") {
	            return allMessages.map(function (message) { return Observable_1.Observable.from([message]); });
	        }
	        else {
	            return [Observable_1.Observable.from(allMessages)];
	        }
	    };
	    SAASimulatorComponent.prototype.updateTotalSelectedRules = function () {
	        //this.logger.debug("thisLoadedRules: "+this.loadedRules.length);
	        this.totalSelectedRules = this.assignment.rules.length == 0 ? this.loadedRules.length : this.selectedRules.length;
	        this.selectAllRules = this.totalSelectedRules == this.assignment.rules.length && this.assignment.rules.length > 0;
	    };
	    SAASimulatorComponent.prototype.rulesToProcess = function () {
	        if (this.assignment.rules.length == 0) {
	            this.logger.debug("Processing a total of " + this.loadedRules.length + " rules. ");
	            return Observable_1.Observable.from(this.loadedRules);
	        }
	        else {
	            this.logger.debug("Processing " + this.selectedRules.length + " selected rules.");
	            return Observable_1.Observable.from(this.selectedRules);
	        }
	    };
